import React from "react";
import {HashRouter, Redirect, Route, Switch} from "react-router-dom";
import Error404 from "./pages/Error404";
import PreJunior from "./pages/PreJunior";
import Junior from "./pages/Junior";
import JuniorPlus from "./pages/JuniorPlus";
import {ToDoBoard} from "./ToDoBoard/ToDoBoard";
import {Chat} from "./Chat/Chat";
import {Hello} from "./Hello/Hello";
import {CustomElementsDemo} from "./CustomElementsDemo/CustomElementsDemo";
import HW4 from "./pages/HW4";

export const PATH = {
  PRE_JUNIOR: "/pre-junior",
  JUNIOR: "/junior",
  JUNIOR_PLUS: "/junior-plus",
  TODOLIST: "/todo",
  CHAT: "/chat",
  HELLO: "/hello",
  CUSTOM_ELEMENTS_DEMO: "/custom-elements-demo",
  HW4: "/HW4",
  GREETINGS: "/greetings"
}

function Routes() {
  return (
      <HashRouter>
        <div>
          {/*Switch –≤—ã–±–∏—Ä–∞–µ—Ç –ø–µ—Ä–≤—ã–π –ø–æ–¥—Ö–æ–¥—è—â–∏–π —Ä–æ—É—Ç*/}
          <Switch>

            <Route path={"/"} exact render={() => <Redirect to={PATH.PRE_JUNIOR}/>}/>{/*–≤ –Ω–∞—á–∞–ª–µ –º—ã –ø–æ–ø–∞–¥–∞–µ–º –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É "/" –∏ –ø–µ—Ä–µ—Ö–æ–¥–∏–º —Å—Ä–∞–∑—É –Ω–∞ —Å—Ç—Ä–∞–Ω–∏—Ü—É PRE_JUNIOR*/}{/*exact –Ω—É–∂–µ–Ω —á—Ç–æ–± —É–∫–∞–∑–∞—Ç—å –ø–æ–ª–Ω–æ–µ —Å–æ–≤–ø–æ–¥–µ–Ω–∏–µ (—á—Ç–æ –ø–æ—Å–ª–µ "/" –Ω–∏—á–µ–≥–æ –Ω–µ –±—É–¥–µ—Ç)*/}

            <Route path={PATH.TODOLIST} render={() => <ToDoBoard/>}/>
            <Route path={PATH.CHAT} render={() => <Chat/>}/>
            <Route path={PATH.HELLO} render={() => <Hello/>}/>
            <Route path={PATH.CUSTOM_ELEMENTS_DEMO} render={() => <CustomElementsDemo/>}/>
            <Route path={PATH.HW4} render={() => <HW4/>}/>

            <Route path={PATH.PRE_JUNIOR} render={() => <PreJunior/>}/>
            <Route path={PATH.JUNIOR} render={() => <Junior/>}/>
            <Route path={PATH.JUNIOR_PLUS} render={() => <JuniorPlus/>}/>

            <Route render={() => <Error404/>}/> {/*—É —ç—Ç–æ–≥–æ —Ä–æ—É—Ç–∞ –Ω–µ—Ç –ø—É—Ç–∏, –æ–Ω –æ—Ç—Ä–∏—Å—É–µ—Ç—Å—è –µ—Å–ª–∏ –ø–æ–ª—å–∑–æ–≤–∞—Ç–µ–ª—å –∑–∞—Ö–æ—á–µ—Ç –ø–æ–ø–∞—Å—Ç—å –Ω–∞ –Ω–µ—Å—É—â–µ—Å—Ç–≤—É—é—â—É—é —Å—Ç—Ä–∞–Ω–∏—Ü—É*/}

          </Switch>
        </div>
      </HashRouter>
  );
}

export default Routes;

