import React, {ChangeEvent, DetailedHTMLProps, FC, InputHTMLAttributes, KeyboardEvent} from 'react';

// —Ç–∏–ø –ø—Ä–æ–ø—Å–æ–≤ –æ–±—ã—á–Ω–æ–≥–æ –∏–Ω–ø—É—Ç–∞
type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

// –∑–¥–µ—Å—å –º—ã –≥–æ–≤–æ—Ä–∏–º —á—Ç–æ —É –Ω–∞—à–µ–≥–æ –∏–Ω–ø—É—Ç–∞ –±—É–¥—É—Ç —Ç–∞–∫–∏–µ –∂–µ –ø—Ä–æ–ø—Å—ã –∫–∞–∫ —É –æ–±—ã—á–Ω–æ–≥–æ –∏–Ω–ø—É—Ç–∞
// (—á—Ç–æ–± –Ω–µ –ø–∏—Å–∞—Ç—å value: string, onChange: ...; –æ–Ω–∏ —É–∂–µ –≤—Å–µ –æ–ø–∏—Å–∞–Ω—ã –≤ DefaultInputPropsType)
type CustomInputType = DefaultInputPropsType & { // –∏ + –µ—â—ë –ø—Ä–æ–ø—Å—ã –∫–æ—Ç–æ—Ä—ã—Ö –Ω–µ—Ç –≤ —Å—Ç–∞–Ω–¥–∞—Ä—Ç–Ω–æ–º –∏–Ω–ø—É—Ç–µ
  onChangeText?: (value: string) => void
  onPressEnter?: () => void
  errorMessage?: string
  placeHolder?: string
  spanClassName?: string
}


export const CustomInput: FC<CustomInputType> = (
    {
        type, // –¥–æ—Å—Ç–∞—ë–º –∏ –∏–≥–Ω–æ—Ä–∏—Ä—É–µ–º —á—Ç–æ–± –Ω–µ–ª—å–∑—è –±—ã–ª–æ –∑–∞–¥–∞—Ç—å –¥—Ä—É–≥–æ–π —Ç–∏–ø –∏–Ω–ø—É—Ç–∞
        onChange, onChangeText,
        onKeyPress, onPressEnter,
        errorMessage,
        placeHolder = "Type something...",
        className, spanClassName,

        ...restProps// –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã –ø–æ–ø–∞–¥—É—Ç –≤ –æ–±—ä–µ–∫—Ç restProps
    }
) => {

  const onChangeCallback = (e: ChangeEvent<HTMLInputElement>) => {
    onChange && onChange(e); // –ø—Ä–æ–≤–µ—Ä–∫–∞, –µ—Å–ª–∏ –µ—Å—Ç—å –ø—Ä–æ–ø—Å onChange
                             // —Ç–æ –ø–µ—Ä–µ–¥–∞—Ç—å –µ–º—É –µ (–ø–æ—Å–∫–æ–ª—å–∫—É onChange –Ω–µ –æ–±—è–∑–∞—Ç–µ–ª–µ–Ω)

    onChangeText && onChangeText(e.currentTarget.value);
  }

  const onKeyPressCallback = (e: KeyboardEvent<HTMLInputElement>) => {
    onKeyPress && onKeyPress(e);

    e.key === "Enter" // –µ—Å–ª–∏ –Ω–∞–∂–∞—Ç–∞ –∫–Ω–æ–ø–∫–∞ Enter
    && onPressEnter // –∏ –µ—Å—Ç—å –ø—Ä–æ–ø—Å onEnter
    && onPressEnter(); // —Ç–æ –≤—ã–∑–≤–∞—Ç—å –µ–≥–æ
  }

    const inputStyle = `${errorMessage ? "border-red-400 bg-red-100" : "border-gray-500 bg-gray-300"} 
                        flex-grow px-3 py-1 mr-2 text-gray-900 bg-gray-300 border border-gray-500 rounded-md focus:outline-none focus:shadow-outline
                        ${className ? className : ""}` // –∫–∞—Å—Ç–æ–º–Ω—ã–µ —Å—Ç–∏–ª–∏, –µ—Å–ª–∏ –µ—Å—Ç—å, —Ç–æ –ø–µ—Ä–µ–Ω–∞–∑–Ω–∞—á–∞—Ç —Å—É—â–µ—Å—Ç–≤—é—â–∏–µ

    const errorSpanStyle = `text-red-400 pl-3 text-sm ${spanClassName ? spanClassName : ""}`

  return (
      <div className="inline-block text-left">
        <input
          type={"text"}
          onChange={onChangeCallback}
          onKeyPress={onKeyPressCallback}
          className={inputStyle}
          placeholder={placeHolder}

          {...restProps} // –æ—Ç–¥–∞—ë–º –∏–Ω–ø—É—Ç—É –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã –µ—Å–ª–∏ –æ–Ω–∏ –µ—Å—Ç—å (value –Ω–∞–ø—Ä–∏–º–µ—Ä —Ç–∞–º –≤–Ω—É—Ç—Ä–∏)
      />
      <br/>
      {errorMessage && <span className={errorSpanStyle}>{errorMessage}</span>}
      </div>
  )
}
