import React, {ChangeEvent, DetailedHTMLProps, InputHTMLAttributes, useState} from 'react'

type DefaultInputPropsType = DetailedHTMLProps<InputHTMLAttributes<HTMLInputElement>, HTMLInputElement>;

type CustomCheckboxPropsType = DefaultInputPropsType & {
  onChangeChecked?: (checked: boolean) => void
  customSpanClassName?: string
};

export const CustomCheckbox: React.FC<CustomCheckboxPropsType> = (
    {
        type, // –¥–æ—Å—Ç–∞—ë–º –∏ –∏–≥–Ω–æ—Ä–∏—Ä—É–µ–º —á—Ç–æ–± –Ω–µ–ª—å–∑—è –±—ã–ª–æ –∑–∞–¥–∞—Ç—å –¥—Ä—É–≥–æ–π —Ç–∏–ø –∏–Ω–ø—É—Ç–∞
        onChange, onChangeChecked,
        className, customSpanClassName,
        children, // –≤ —ç—Ç—É –ø–µ—Ä–µ–º–µ–Ω–Ω—É—é –ø–æ–ø–∞–¥—ë—Ç —Ç–µ–∫—Å—Ç, —Ç–∏–ø–∏–∑–∏—Ä–æ–≤–∞—Ç—å –Ω–µ –Ω—É–∂–Ω–æ —Ç–∞–∫ –∫–∞–∫ –æ–Ω –∑–∞—Ç–∏–ø–∏–∑–∏—Ä–æ–≤–∞–Ω –≤ React.FC

        ...restProps// –≤—Å–µ –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã –ø–æ–ø–∞–¥—É—Ç –≤ –æ–±—ä–µ–∫—Ç restProps

}
) => {
  const [value, setValue] = useState<boolean>(false)
  const onChangeHandler = (e: ChangeEvent<HTMLInputElement>) => {
    setValue(e.currentTarget.checked)
    onChangeChecked && onChangeChecked(e.currentTarget.checked)
  }

  const checkboxStyle = `${className ? className : ""}`
  const spanStyle = `pl-1 ${customSpanClassName ? customSpanClassName : ""}`

  return (
      <label>
        <input
            type="checkbox"
            className={checkboxStyle}
            checked={value}
            onChange={onChangeHandler}

            {...restProps} // –æ—Ç–¥–∞—ë–º –∏–Ω–ø—É—Ç—É –æ—Å—Ç–∞–ª—å–Ω—ã–µ –ø—Ä–æ–ø—Å—ã –µ—Å–ª–∏ –æ–Ω–∏ –µ—Å—Ç—å (checked –Ω–∞–ø—Ä–∏–º–µ—Ä —Ç–∞–º –≤–Ω—É—Ç—Ä–∏)
        />
        {children && <span className={spanStyle}>{children}</span>}
      </label> // –±–ª–∞–≥–æ–¥–∞—Ä—è label –Ω–∞–∂–∞—Ç–∏–µ –Ω–∞ —Å–ø–∞–Ω –ø–µ—Ä–µ–¥–∞—Å—Ç—Å—è –≤ –∏–Ω–ø—É—Ç
  )
}